<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">City</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/city">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>
            List
          </v-btn>
        </router-link>
      </div>
    </div>
    <template>
      <div class="card">
        <div class="card-body my-2">
          <v-form ref="form" v-model="valid">
            <v-select :items="country" label="Parent Country" prepend-inner-icon="mdi-home-city"></v-select>
            <v-autocomplete class="my-0 py-0"
                            v-model="cityform.stateId"
                            label="Select State"
                            :items="locations"
                            item-text="state"
                            :rules="State"
                            prepend-inner-icon="mdi-home-city"
                            item-value="id">
            </v-autocomplete>
            <v-text-field label="City Name" v-model="cityform.cityname" :rules="cityname"></v-text-field>
            <div class="my-2">
              <v-btn color="success" class="mr-4" :disabled="!valid" @click="addcity">SAVE</v-btn>
              <v-btn color="error" class="mr-4" @click="reset">CANCEL</v-btn>
            </div>
          </v-form>
        </div>
      </div>
    </template>
    <v-snackbar v-model="snackbar" :timeout="timeout" type="success" border="left" elevation="3">
      <template v-slot:action="{ attrs }">
        <v-btn color="blue"  v-bind="attrs" text @click="snackbar = false">Close</v-btn>
      </template>
      {{text}}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CreateCity",
  components: {  },
  data: () => ({
    country: ['India'],
    locations: [],
    cityform:{
      stateId: '',
      cityname: '',
    },
    valid: false,
    snackbar: false,
    text: '',
    timeout: -1,
    show1: false,
    State: [
      v => !!v || 'State is required.',
    ],
    cityname: [
      v => !!v || 'City Name is required.',
    ],
  }),
    mounted() {
      this.getState();
    },
  methods: {
    getState() {
      axios.get("allstates")
          .then((resp) => {
            this.locations = resp.data.locations;
          });
    },
    reset(){
      this.$refs.form.reset();
    },
    addcity() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
        let data = {
        stateId:  this.cityform.stateId,
        cityname:  this.cityform.cityname,
        city_status: 1,
        };
        axios.post("add/city",data,config)
            .then((resp) => {
              if(resp.data.message) {
                this.snackbar = true;
                this.text = resp.data.message;
                this.timeout = 3000;
                this.$refs.form.reset();
              }
            })
            .catch(error => {
              // Handle the error
              var data = error.toJSON();
              if(data.status == 400){
                this.snackbar = true;
                this.text = "something went wrong.";
                this.timeout = 3000;
              }
            });
         this.$refs.form.validate();
      },
  },
};
</script>
